@import "../../styles/shared";

#about {
  background-color: $reverse-font-color;
  padding: 5px;
  width: 100%;
  .about__container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 0 auto;
    width: 80%;

    .aboutSection__left,
    .aboutSection__right {
      padding: 2rem 0;
      flex: 0 0 45%;
      @media (max-width: 600px) {
        flex: 0 0 100%;
      }
    }
    .aboutSection__right {
      width: 100%;
      img {
        width: 100%;
      }
    }

    padding: 1rem 0;
  }
}
